* {
	font-family: Arial, Helvetica, sans-serif;
	background-color: #170e4b;
	color: #FFFF
}

body {
	padding: 6rem;
}

input{
	background-color: grey;
	color:black;
	width: 100%;
	font-size: 1.3rem;
}

.card-grid {
	display: flex;
	flex-direction: row;
}
.card {
	display: flex;
	flex-direction: column;
	border: 1px solid grey;
	border-radius: 6px;
	margin-bottom: 10px;
	margin-right: 10px;
	overflow: hidden;
}

.card p{
	padding: 10px;
	text-align: center;
}

.card img{
	max-height: 300px;
	max-width: 300px;
}